var list = [];
list.push({ title: '', data: [] })
list.push({ title: '', data: [] })
list.push({ title: '', data: [] })

var namelist0 = [
    "", "", "", "", "",
    "", "", "", "", "",
    "", "", "", "", "",
    "", "", "", "", "",
    "", "", "", "", "",
    "", "", "", "", "",
    "52", ""
]

var namelist1 = [
    "", "", "", "", "",
    "", "", "", "", "",
    "", "", "", "", "",
    "", "", ""
]
var namelist2 = [
    "91", "", "", "", "",
    "", "", "", "", "",
    "", "", "", "AIMIDI", "",
    "", "360", "", "", "",
    "", "", "", "", ""
]
for (var i = 1; i <= 32; i++) {
    if (i < 10) {
        var name = '0' + i;
    } else {
        var name = i;
    }
    var obj = { title: namelist0[i - 1], img: require('../../assets/ptjr/' + name + '.png') };

    var groupnum = Math.floor((i - 1) / 15);
    if (!list[0].data[groupnum]) {
        list[0].data[groupnum] = [];
    }
    list[0].data[groupnum].push(obj);
}
for (var i = 1; i <= 18; i++) {
    if (i < 10) {
        var name = '0' + i;
    } else {
        var name = i;
    }
    var obj = { title: namelist1[i - 1], img: require('../../assets/ptsj/' + name + '.png') };

    var groupnum = Math.floor((i - 1) / 15);
    if (!list[1].data[groupnum]) {
        list[1].data[groupnum] = [];
    }
    list[1].data[groupnum].push(obj);
}
for (var i = 1; i <= 25; i++) {
    if (i < 10) {
        var name = '0' + i;
    } else {
        var name = i;
    }
    var obj = { title: namelist2[i - 1], img: require('../../assets/ptzx/' + name + '.png') };

    var groupnum = Math.floor((i - 1) / 15);
    if (!list[2].data[groupnum]) {
        list[2].data[groupnum] = [];
    }
    list[2].data[groupnum].push(obj);
}

export default list



